body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0a192f;
  color: var(--slate);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* CSS Custom Properties */
:root {
  --dark-navy: #020c1b;
  --navy: #0a192f;
  --light-navy: #112240;
  --lightest-navy: #233554;
  --navy-shadow: rgba(2, 12, 27, 0.7);
  --dark-slate: #495670;
  --slate: #8892b0;
  --light-slate: #a8b2d1;
  --lightest-slate: #ccd6f6;
  --white: #e6f1ff;
  --green: #64ffda;
  --green-tint: rgba(100, 255, 218, 0.1);
  --pink: #f57dff;
  --blue: #57cbff;
  --html5-orange: rgb(208, 63, 31);
  --css3-blue: rgb(32, 65, 213);
  --js-yellow: rgb(226, 203, 66);
  --bs-purple: rgb(107, 19, 237); 
  --mongo-green: rgb(0, 222, 84);
  --fs-xxs: 12px;
  --fs-xs: 13px;
  --fs-sm: 14px;
  --fs-md: 16px;
  --fs-lg: 18px;
  --fs-xl: 20px;
  --fs-xxl: 22px;
  --fs-heading: 32px;
  --border-radius: 4px;
  --nav-height: 50px;
}

a {
  text-decoration: none;
  color: inherit
}

a:hover {
  color: inherit;
}

h1 {
  color: var(--green);
}

.align-heading {
  text-align: left;
  margin-bottom: 20px;
}

.center-btn {
  display: flex;
}

.span-2 {
  grid-column: span 2;
}